import React from 'react';
import PropTypes from 'prop-types';
import connect from '@vkontakte/vkui-connect';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon24Replay from '@vkontakte/icons/dist/24/replay';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon32Poll from '@vkontakte/icons/dist/32/poll';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import './assets/css/bootstrap.css';
import './assets/css/style.css';
import '@vkontakte/vkui/dist/vkui.css';
import { Panel, ListItem, Button, Group, Div, Avatar, Cell, List , Alert} from '@vkontakte/vkui';

const Home = ({ id, add1, userAcc, clicks, reputation, fetchedUser, go, priv, name, dark, light }) => (
	<Panel id="home">
		<Div>
			<div className="row">
				<div className="col-md-12">
					<div className="text-center text-muted">
						<h1>New Life</h1>
					</div>
					<div className="text-center buttons">
						<div className="d-flex justify-content-around">
							<div className="justify-content-around">
								<button className="btn btn-rating">
									<Icon32Poll className="buttons_custom" onClick={go} data-to="top" />
								</button>
								<p className="text-buttons text-muted text-monospace">Топ</p>
							</div>
							<div className="justify-content-around">
								<button className="btn btn-exchange">
									<Icon24Filter className="buttons_custom" onClick={go} data-to="dop" />
								</button>
								<p className="text-buttons text-muted text-monospace">Разное</p>
							</div>
							<div className="justify-content-around">
								<button className="btn btn-shop" onClick={go} data-to="persik">
									<Icon24Flash className="buttons_custom" />
								</button>
								<p className="text-buttons text-muted text-monospace">Магазин</p>
							</div>
							<div className="justify-content-around">
								<button className="btn btn-kat" onClick={go} data-to="katalog">
									<Icon24Game className="buttons_custom" />
								</button>
								<p className="text-buttons text-muted text-monospace">Каталог</p>
							</div>
						</div>
					</div>
				</div>
				<div className="container">
					<Group title="Информация">
					
		{fetchedUser &&
		<Group>
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`} 
				{fetchedUser &&
				<Div>
				ID: {`${fetchedUser.id}`}
			</Div>
				}
			</ListItem>
		</Group>}
            <List>
                            <Cell before={<Icon24Mention className="CellIconCustom" />} asideContent={userAcc && <b>{`${userAcc.nickname}`}</b>} onClick={go} data-to="changeNickname"> {/* {userAcc.nickname !== null ? userAcc.nickname : `${fetchedUser.first_name} ${fetchedUser.last_name}`} */}
                Никнейм: 
            </Cell>
            <Cell before={<Icon24User className="CellIconCustom" />} asideContent={<b>{priv ? priv : 'Пользователь'}</b>}>
                Донат:
            </Cell>
            <Cell before={<Icon24Coins className="CellIconCustom" />} asideContent={<b>{clicks}</b>}>
                Баланс:
            </Cell>
            <Cell before={<Icon24Favorite className="CellIconCustom" />} asideContent={<b>{reputation}</b>}>
                Рейтинг:
            </Cell>
        </List>
                </Group>
                <Group title="Майнить коины">
                    <Div>
                        <Button size="xl" level="primary" onClick={add1}>Клик</Button>
                    </Div>
					<Div>
                        <Button size="xl" level="primary" onClick={add1}>Бонус</Button>
                    </Div>
                </Group>
				</div>
			</div>
		</Div>
	</Panel>
);



Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
    }),
};
export default Home;
