import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, FormLayout, Button, Div, Input, FormLayoutGroup } from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24LogoTwitter from '@vkontakte/icons/dist/24/logo_twitter';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';

const osname = platform();

const Transfer = ({ go, handleTransferChange, handleTransferSubmit, userAcc }) => (
    <Panel id="Transfer">
        <PanelHeader
            left={<HeaderButton onClick={go} data-to="dop">
                {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
            </HeaderButton>}
        >
            Перевод коинов
		</PanelHeader>
        <FormLayout>
            <FormLayoutGroup top="Введите айди">
                <Input type="text" type="number" name="toId" defaultValue="" onChange={handleTransferChange} />
            </FormLayoutGroup>
            <FormLayoutGroup top="Введите кол-во">
                <Input type="text" type="number" name="transferCoins" defaultValue="1" onChange={handleTransferChange} />
            </FormLayoutGroup>
        </FormLayout>
        <Div>
            <Button size="xl" level="primary" onClick={handleTransferSubmit}>Отправить</Button>
        </Div>
    </Panel>
);

Transfer.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Transfer;
